export async function fetchRemoteStaticConfigVersion( url : string ): Promise<number | null> {


  const res = await fetch(url, { cache: "no-store" });
  if (!res.ok) return null;

  const text = await res.text();

  // matches: "VERSION" : 2  OR  "VERSION": "2"
  const match = text.match(/"VERSION"\s*:\s*("?)([^,\n}]+)\1\s*,?/);

  if (!match) return null;

  const rawValue = match[2].trim().replace(/^"|"$/g, "");
  const num = Number(rawValue);

  return Number.isFinite(num) ? num : null;
}


export async function fetchRemoteStaticConfig( url : string ): Promise<number | null> {


  const res = await fetch(url, { cache: "no-store" });
  if (!res.ok) return null;

  const text = await res.text();

  const cleaned : string = extractMainContentObject(text) as string

  return JSON.parse(cleaned)
}



 
export function extractMainContentObject(text: string) {
  const marker = "window.MAIN_CONTENT";

  // remove EVERYTHING before `window.MAIN_CONTENT =`
  const idx = text.indexOf(marker);
  if (idx === -1) return null;

  let cleaned : string = text.slice(idx);



  // remove `window.MAIN_CONTENT =`
  cleaned = cleaned.replace(/^window\.MAIN_CONTENT\s*=\s*/m, "").trim();

  // remove trailing semicolon
  cleaned = cleaned.replace(/;\s*$/, "").trim();

  //  remove trailing commas before } or ]
  cleaned = cleaned.replace(/,\s*([}\]])/g, "$1");
  
  return cleaned

}
