import React, { useEffect, useMemo, useRef, useState } from "react";
import { Select, Box, Button, MantineProvider, Switch, Stack } from "@mantine/core";
import PageEditorDispatcher from "./PageEditorDispacher";
import { getPrev } from "../../content/settings-util";
import { setSuperAdminState } from "./permissionManager";
import { useStorage } from "../../hooks/useStorage";
import {
  fetchRemoteStaticConfig,
} from "../../../plugins/remote-content/src/components/ppm/static-content/fetchRemoteServerConfigVersion";
import { contentDynamic } from "../../content/settings";
import { pushRemoteContent } from "../ppm/remote-content/remoteContent";

type LangCode = "en" | "vn";

type Props = {
  data: any;
  //  Parent provides this to overwrite metadata in the parent state
  onMetadataSave?: (nextMetadata: any) => void;
};

/* ===============================
   METADATA  FILE RESOLUTION
================================ */
function hydrateMetadata(node: any, lang: LangCode, recentContent?: any): any {
  if (!node || typeof node !== "object") return node;

  if (node.source) {
    const path = node.source.replace(/^pages\.content(EN|VN)\./, "");

    //  use recentContent if available
    if (recentContent) {
      const v = getValueFromRecentContent(recentContent, path, lang);
      if (v !== undefined) return v;
    }

    // fallback to old system
    return getPrev(path, lang);
  }

  if (node.array) {
    return Object.values(node.array).map((c) => hydrateMetadata(c, lang, recentContent));
  }

  const out: any = {};
  for (const [k, v] of Object.entries(node)) {
    if (k === "settings") continue;
    out[k] = hydrateMetadata(v, lang, recentContent);
  }
  return out;
}

function getValueFromRecentContent(recentContent: any, path: string, lang: LangCode) {
  const langKey = lang === "en" ? "contentEN" : "contentVN";

  // recentContent.CONTENT.contentEN.pages.globals.section.headerContent...
  const root = recentContent?.CONTENT?.[langKey];
  if (!root) return undefined;

  const keys = path.split(".");
  let cursor = root;

  for (const k of keys) {
    cursor = cursor?.[k];
    if (cursor === undefined) return undefined;
  }

  return cursor;
}

/* ===============================
   METADATA PATH MUTATOR
================================ */
function toggleMetadataLockedBySource(metadataRoot: any, source: string) {
  if (!metadataRoot || typeof metadataRoot !== "object") return metadataRoot;

  const walk = (node: any): boolean => {
    if (!node || typeof node !== "object") return false;

    if (node.source === source && node.settings) {
      node.settings.locked = !(node.settings.locked ?? false);
      return true;
    }

    if (node.array) {
      for (const child of Object.values(node.array)) {
        if (walk(child)) return true;
      }
    }

    for (const [k, v] of Object.entries(node)) {
      if (k === "settings") continue;
      if (walk(v)) return true;
    }

    return false;
  };

  walk(metadataRoot);
  return metadataRoot;
}

/* ===============================
   META  EDIT SHAPE (NO HIDING)
================================ */
function zipMeta(
  meta: any,
  resolved: any,
  valueMap: React.MutableRefObject<Record<string, any>>,
  superAdmin: boolean,
  onToggleLock: (source: string) => void,
  triggerUpdate: () => void
): any {
  if (!meta || typeof meta !== "object") return null;

  if (meta.settings && meta.source) {
    const source = meta.source;

    //  ONLY metadata decides locked now
    const metadataLocked = meta.settings.locked ?? false;

    // Effective lock:
    // - Admin mode: locked means locked
    // - Superadmin mode: always editable
    const effectiveLocked = metadataLocked && !superAdmin;

    return {
      settings: {
        ...meta.settings,
        metadataLocked,
        userLocked: metadataLocked, // kept for UI compatibility
        locked: effectiveLocked,
        superAdmin,
        onToggleLock: superAdmin ? () => onToggleLock(source) : undefined,
      },
      defaultValue: resolved,
      source,
      onChange: (v: any) => {
        if (!effectiveLocked) {
          valueMap.current[source] = v;
          triggerUpdate();
        }
      },
    };
  }

  if (meta.array && Array.isArray(resolved)) {
    const metadataLocked = meta.settings?.locked ?? false;
    const effectiveLocked = metadataLocked && !superAdmin;

    return {
      settings: {
        ...meta.settings,
        locked: effectiveLocked,
        superAdmin,
      },
      array: resolved.map((item: any, i: number) =>
        zipMeta(meta.array[i], item, valueMap, superAdmin, onToggleLock, triggerUpdate)
      ),
    };
  }

  const out: any = {};
  for (const [k, m] of Object.entries(meta)) {
    if (k === "settings") continue;
    out[k] = zipMeta(m, resolved?.[k], valueMap, superAdmin, onToggleLock, triggerUpdate);
  }

  if (meta.settings) {
    const metadataLocked = meta.settings.locked ?? false;
    const effectiveLocked = metadataLocked && !superAdmin;

    out.settings = {
      ...meta.settings,
      superAdmin,
      locked: effectiveLocked,
    };
  }

  return out;
}

/* ===============================
   MAIN COMPONENT
================================ */
export default function PageEditor({ data, onMetadataSave }: Props) {
  const { metadata } = data;

  //  Editable metadata state (locks now live here)
  const [metadataState, setMetadataState] = useState<any>(() =>
    JSON.parse(JSON.stringify(metadata))
  );

  const [recentContent, setRecentContent] = useState<any>(undefined);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const valueMap = useRef<Record<string, any>>({});
  const storage = useStorage();

  const [superAdmin, setSuperAdmin] = useState<boolean>(() => {
    if (!storage) return false;
    try {
      const raw = storage.getItem("cms-superadmin-state");
      return raw ? JSON.parse(raw) : false;
    } catch {
      return false;
    }
  });

  const [lang, setLang] = useState<LangCode>(
    metadataState?.contentEN ? "en" : "vn"
  );

  const [page, setPage] = useState<string | null>(null);
  const [section, setSection] = useState<string | null>(null);

  const langKey = lang === "en" ? "contentEN" : "contentVN";
  const activeMeta = metadataState?.[langKey];

  const pages = useMemo(() => Object.keys(activeMeta?.pages ?? {}), [activeMeta]);

  const sections = useMemo(() => {
    if (!page) return [];
    return Object.keys(activeMeta?.pages?.[page]?.section ?? {});
  }, [activeMeta, page]);

  useEffect(() => {
    if (page !== null) return;
    const p = pages[0] ?? null;
    const s = p && Object.keys(activeMeta?.pages?.[p]?.section ?? {})[0];
    setPage(p);
    setSection(s ?? null);
  }, [pages, activeMeta, page]);

  const sectionMeta = activeMeta?.pages?.[page ?? ""]?.section?.[section ?? ""];

  const hydrated = useMemo(() => {
    return sectionMeta ? hydrateMetadata(sectionMeta, lang, recentContent) : null;
  }, [sectionMeta, lang, recentContent]);

  const [updateCounter, setUpdateCounter] = useState(0);

  const hydratedWithChanges = useMemo(() => {
    if (!hydrated) return null;
    const merged = JSON.parse(JSON.stringify(hydrated));

    for (const [source, val] of Object.entries(valueMap.current)) {
      const path = source.replace(/^pages\.content(EN|VN)\./, "");
      const keys = path.split(".");
      let cursor = merged;

      for (let i = 0; i < keys.length - 1; i++) {
        cursor = cursor?.[keys[i]];
        if (!cursor) break;
      }
      if (cursor) cursor[keys[keys.length - 1]] = val;
    }

    return merged;
  }, [hydrated, updateCounter]);

  //  Persist superadmin toggle (you didn't ask to remove this)
  useEffect(() => {
    setSuperAdminState(superAdmin);
  }, [superAdmin]);

  // Load most recent content (unchanged)
// Load most recent content (FIXED: never choose null/undefined)
useEffect(() => {
  (async () => {
    const isValidMainContent = (x: any) => {
      return !!(
        x &&
        typeof x === "object" &&
        x.CONTENT &&
        typeof x.CONTENT === "object" &&
        (x.CONTENT.contentEN?.pages || x.CONTENT.contentVN?.pages)
      );
    };

    let localStorageAdmin: any = undefined;

    try {
      localStorageAdmin = JSON.parse(storage?.getItem("admin") ?? "null");
    } catch {
      localStorageAdmin = null;
    }

    const previewRemoteContent = await fetchRemoteStaticConfig(
      "https://preview.tinhhoaviet.org.vn/static-content/static-config.js"
    );

    const websiteRemoteContent = await fetchRemoteStaticConfig(
      "https://www.tinhhoaviet.org.vn/static-content/static-config.js"
    );

    const candidates = [
      { name: "localStorage", value: localStorageAdmin },
      { name: "localFile", value: contentDynamic },
      { name: "previewRemote", value: previewRemoteContent },
      { name: "websiteRemote", value: websiteRemoteContent },
    ]
      .filter((x) => isValidMainContent(x.value))
      .sort((a, b) => (b.value?.VERSION ?? -1) - (a.value?.VERSION ?? -1));

    if (candidates.length === 0) {
      console.log(" No valid content sources found");
      setRecentContent(undefined);
      return;
    }

    console.log(" Selected content source:", candidates[0].name);
    console.log("VERSION:", candidates[0].value?.VERSION);

    setRecentContent(candidates[0].value);
  })();
}, [storage]);


  const handleSubmit = async (obj: any, onSuccess?: any) => {
    if (isSubmitting) return;

    try {
      setIsSubmitting(true);
      const result: any = await pushRemoteContent(obj);

      console.log(" Update successful!");
      console.log(`newSha: ${result?.newSha || "unknown"}`);
      console.log(`usedFallback: ${String(result?.usedFallback ?? false)}`);
    } catch (err: any) {
      console.log(` Update failed: ${err?.message || String(err)}`);
    } finally {
      setIsSubmitting(false);
      if (onSuccess) onSuccess();
    }
  };

  //  Toggle lock updates metadataState ONLY
  const handleToggleLock = (source: string) => {
    setMetadataState((prevMeta: any) => {
      const cloned = JSON.parse(JSON.stringify(prevMeta));
      toggleMetadataLockedBySource(cloned, source);
      return cloned;
    });

    setUpdateCounter((prev) => prev + 1);
  };

  const editorKey = `${lang}:${page}:${section}:${recentContent?.VERSION ?? 0}`;

  if (!recentContent) {
    return (
      <MantineProvider>
        <Box p="md">
          <div style={{ fontSize: 14 }}>Loading recentContent...</div>
        </Box>
      </MantineProvider>
    );
  }

  return (
    <MantineProvider>
      <Box p="md">
        <Stack spacing="md">
          {/* <Switch
            label={superAdmin ? "Superadmin" : "Admin"}
            checked={superAdmin}
            onChange={(e) => setSuperAdmin(e.currentTarget.checked)}
          /> */}

          <Select
            label="Language"
            data={[
              { value: "en", label: "English" },
              { value: "vn", label: "Vietnamese" },
            ]}
            value={lang}
            onChange={(v) => setLang(v as LangCode)}
          />

          <Select label="Page" data={pages.filter( x => {
        
            if(x?.includes('settings')) return false 
            else return true 
         

          })} value={page} onChange={setPage} />
      
          <Select label="Section" data={sections.filter( x => {
            console.log(`page: ${page} : ${x}`)
            if(page?.includes('landing') && x?.includes('settings')) return false 
            if(page?.includes('landing') && x?.includes('eventContent')) return false 
            if(page?.includes('globals') && x?.includes('settings')) return false 
                if(page?.includes('process-nomination') && x?.includes('settings')) return false 
            else return true 
         

          })} value={section} onChange={setSection} />
        </Stack>

        {hydratedWithChanges && (
          <PageEditorDispatcher
            // key={editorKey}
            value={zipMeta(
              sectionMeta,
              hydratedWithChanges,
              valueMap,
              superAdmin,
              handleToggleLock,
              () => setUpdateCounter((prev) => prev + 1)
            )}
          />
        )}

        {/* Preview */}
        <Button
          mt="lg"
          onClick={() => {
            const cloned = JSON.parse(JSON.stringify(recentContent));

            for (const [source, val] of Object.entries(valueMap.current)) {
              const path = source.replace(/^pages\.content(EN|VN)\./, "");
              const keys = path.split(".");

              let cursor = cloned?.CONTENT?.[langKey];

              for (let i = 0; i < keys.length - 1; i++) {
                cursor = cursor?.[keys[i]];
                if (!cursor) break;
              }
              if (!cursor) continue;

              cursor[keys[keys.length - 1]] = val;
            }

            if (storage) {
              const updated: any = {
                ...cloned,
                meta: {
                  version: (cloned?.meta?.version ?? 0) + 1,
                  source: "local-storage",
                },
              };
              storage.setItem("admin", JSON.stringify(updated));
              setRecentContent(updated);
            }
          }}
        >
          Preview
        </Button>

        {/* Delete draft content */}
        <Button
          style={{ marginLeft: 10 }}
          mt="lg"
          onClick={() => {
            if (storage) storage.setItem("admin", "");
          }}
        >
          Delete Current Draft
        </Button>

        {/* Admin Save: overwrite metadata in parent */}
        {/* <Button
          style={{ marginLeft: 10 }}
          mt="lg"
          onClick={() => {
            console.log(" Admin Save: metadataState updated");
            console.log(metadataState);

            // Pass back to parent (this is the overwrite you asked for)
            onMetadataSave?.(metadataState);
          }}
        >
          Admin Save
        </Button> */}

        {/* Submit */}
        <Button
          mt="lg"
          onClick={() =>
            handleSubmit(recentContent, () => {
              if (storage) storage.setItem("admin", "");
            })
          }
          disabled={isSubmitting}
          style={{
            padding: "10px 14px",
            marginLeft: 10,
            background: isSubmitting ? undefined : "black",
            cursor: isSubmitting ? "not-allowed" : "pointer",
          }}
        >
          {isSubmitting ? "Submitting..." : "Submit"}
        </Button>
      </Box>
    </MantineProvider>
  );
}
