/**
 * Permission Manager
 * Handles localStorage-based permission state for CMS editor
 * 
 * Data model: "cms_permissions_v1" = { path: locked_boolean }
 * Example: { "pages.contentEN.process-nomination.bgimage": true }
 */

const PERMISSION_KEY = "cms_permissions_v1";
const SUPERADMIN_KEY = "cms-superadmin-state";

/**
 * Load all permissions from localStorage
 * Returns a map of { source_path: locked_boolean }
 */
export function loadPermissions(): Record<string, boolean> {
  if (typeof window === "undefined") return {};
  
  try {
    const stored = localStorage.getItem(PERMISSION_KEY);
    return stored ? JSON.parse(stored) : {};
  } catch (e) {
    console.error("[permissionManager] Failed to load permissions:", e);
    return {};
  }
}

