/**
 * Check if a specific path is locked
 * Returns the locked state from current in-memory state
 */
export function isLocked(path: string, lockMap: Record<string, boolean>): boolean {
  return !!lockMap[path];
}

/**
 * Load superadmin state from localStorage
 */
export function loadSuperAdminState(): boolean {
  if (typeof window === "undefined") return false;
  
  try {
    const stored = localStorage.getItem(SUPERADMIN_KEY);
    return stored ? JSON.parse(stored) : false;
  } catch (e) {
    console.error("[permissionManager] Failed to load superadmin state:", e);
    return false;
  }
}

/**
 * Save superadmin state to localStorage
 */
export function setSuperAdminState(state: boolean): void {
  if (typeof window === "undefined") return;
  
  try {
    localStorage.setItem(SUPERADMIN_KEY, JSON.stringify(state));
  } catch (e) {
    console.error("[permissionManager] Failed to save superadmin state:", e);
  }
}

/**
 * Clear all permissions (for reset scenarios)
 */
export function clearAllPermissions(): void {
  if (typeof window === "undefined") return;
  
  try {
    localStorage.removeItem(PERMISSION_KEY);
  } catch (e) {
    console.error("[permissionManager] Failed to clear permissions:", e);
  }
}
